    box-sizing: border-box;
}
.header {
    text-align: center;
    padding: 10px;
    margin-bottom: 10px;
    position: relative;
}
.title {
    color: blue;
    font-size: 40px;
    font-weight: bold;
}
.subtitle {
    font-size: 20px;
    font-weight: bold;
}

